.container{
    text-align: center;
}
.title{
    margin-top: 30px;
    color: white;
    font-size: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}   
.title span{
    color:#26ffcb;
    padding-left: 15px;
}

.reset{
    width:200px ;
    height:100px ;
    border:none ;
    outline:none ;
    cursor:pointer ;
    background-color: #1f3540;
    font-size: 20px;
    color: #26ffcb;
    border-radius: 50px;
    margin-top: -30px;
   

}
 .boxes{
    display:flex;
    height: 120px;
    width: 120px;
    background-color: #1f3540;
    border: 4px solid #0f1b21;
    border-radius: 12px;
    cursor: pointer;
 }

 .board{
    height: 500px;
    width: 300px;
    display: flex;
    margin: auto;

 }

 .boxes img{
    margin: 30px 30px;
 }
